const user = 'admin'
const pass = 'admin!'

const state = {
  authenticated: false,
}

const getters = {
  isAuthenticated: (state) => state.authenticated,
  user: () => user,
}

const mutations = {
  signin: (state) => {
    state.authenticated = true
  },
  signout: (state) => {
    state.authenticated = false
  }
}

const actions = {
  signin: ({commit}, creds) => {
    if (creds.user == user && creds.pass == pass) {
      commit('signin')
      return Promise.resolve(true)
    }
    return Promise.reject(false)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
