<template>
  <table class="table table-hover">
    <thead>
      <tr>
        <th class="text-center">#</th>
        <th>client</th>
        <th class="text-center">date de livraison/retrait</th>
        <th class="text-center">livraison</th>
        <th class="text-center">retrait</th>
        <th class="text-center">clôturé</th>
        <th class="text-center">montant</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(o, i) in orders" :key="i">
        <td class="text-center">{{i+1}}</td>
        <td>{{o.contact.name}}</td>
        <td class="text-center">{{o.when}}</td>
        <td class="text-center">
          <i v-if="!o.closed" :class="['bi', {'bi-square': !isDelivery(o), 'bi-check-square': isDelivery(o)}]"></i>
        </td>
        <td class="text-center">
          <i v-if="!o.closed" :class="['bi', {'bi-square': !isTakeaway(o), 'bi-check-square': isTakeaway(o)}]"></i>
        </td>
        <td class="text-center">
          <i :class="['bi', {'bi-square': !o.closed, 'bi-check-square': o.closed}]"></i>
        </td>
        <td class="text-center">{{total(o.products)}}&euro;</td>
        <td class="align-middle">
          <router-link :to="{name: 'edit.order', params: {id: i}}" class="btn btn-sm btn-outline-secondary">
            <i class="bi bi-pencil-square"></i>
          </router-link>
        </td>
      </tr>
    </tbody>
  </table>
</template>
<script>
import {mapGetters} from 'vuex'

export default {
  name: 'History',
  computed: {
    ...mapGetters('orders', ['orders']),
  },
  methods: {
    total(ps) {
      return ps.reduce((p, c) => p + (c.quantity*c.price), 0)
    },
    isTakeaway(order) {
      return order.address == undefined
    },
    isDelivery(order) {
      return order.address != undefined
    },
  },
}
</script>
