<template>
  <main class="container">
    <form class="mx-auto" @submit.prevent="authenticate">
        <h1 class="h3 mb-3 fw-normal text-center">Identifiez vous</h1>
        <aside class="alert alert-danger text-center" v-if="oups">
          <p>nom d'utilisateur/mot de passe incorrect</p>
          <p>veuillez réessayer!!!</p>
        </aside>
        <div class="form-floating mb-3">
          <input type="text"
            class="form-control rounded-0"
            id="user"
            placeholder="admin"
            v-model="user">
          <label for="floatingInput">Nom d'utilisateur</label>
        </div>
        <div class="form-floating mb-3">
          <input type="password"
            class="form-control rounded-0"
            id="pass"
            placeholder="leshangarsdelafermedelacourt"
            v-model="pass">
          <label for="floatingPassword">Mot de passe</label>
        </div>
        <button class="w-100 btn btn-lg btn-primary mt-3" type="submit" @click="authenticate">Identifiez vous</button>
        <p class="my-3 text-muted text-end">&copy; 2021</p>
      </form>
  </main>
</template>
<script>
import {mapActions} from 'vuex'

export default {
  name: 'Signin',
  data() {
    return {
      user: "",
      pass: "",
      oups: false,
    }
  },
  methods: {
    ...mapActions('auth', ['signin']),
    authenticate() {
      this.signin({user: this.user, pass: this.pass}).then(() => {
        this.oups = false
        this.$router.push({name: 'dashboard'})
      }).catch(() => {
        this.oups = true
      })
    },
  }
}
</script>
<style scoped>
form{
  width: 320px;
  margin-top: 180px;
}
</style>
