<template>
<div class="row">
  <label class="col col-form-label">prix</label>
  <div class="col-3">
    <input type="number" class="form-control" :value="price.amount">
  </div>
  <label class="col col-form-label">quantité</label>
  <div class="col-2">
    <input type="number" class="form-control" min="1" :value="price.quantity">
  </div>
  <label class="col col-form-label">unité/pièces</label>
  <div class="col-2">
    <input type="text" class="form-control" :value="price.unit">
  </div>
  <div class="col">
    <div class="btn-group ms-auto" role="group">
      <button v-if="create" type="button"
        class="btn btn-outline-primary"
        @click="$emit('add:price', price)">
        <i class="bi bi-plus"></i>
      </button>
      <button v-else type="button"
      class="btn btn-outline-danger"
      title="supprimer la ligne courante"
      @click="$emit('delete:price', price)">
        <i class="bi bi-dash"></i>
      </button>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'ProductPriceEdit',
  props: {
    price: Object,
    create: {
      type: Boolean,
      default: false,
    }
  },
  emits: ['update:price', 'delete:price', 'add:price'],
}
</script>
