<template>
  <div class="row">
    <label class="col col-form-label">nom</label>
    <div class="col-10">
      <input type="text" class="form-control" :value="category" @input="$emit('update:category', $event.target.value)">
    </div>
    <div class="col">
      <div class="btn-group ms-auto" role="group">
        <button v-if="create" type="button"
          class="btn btn-outline-primary"
          @click="$emit('add:category', category)">
          <i class="bi bi-plus"></i>
        </button>
        <button v-else type="button"
        class="btn btn-outline-danger"
        title="supprimer la ligne courante"
        @click="$emit('delete:category', category)">
          <i class="bi bi-dash"></i>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ProductCategoryEdit',
  props: {
    category: String,
    create: {
      type: Boolean,
      default: false,
    }
  },
  emits: ['update:category', 'delete:category', 'add:category'],
}
</script>
